import * as nconf from "nconf";
import * as path from "path"
import * as fs from "fs"

class Config {
    public static NODEENV = "NODE_ENV";
    public static PAGETRACK = "PAGETRACK";
    public static PORT = "PORT";
    public static DBADDRESS = "DBADDRESS";
    public static DBPORT = "DBPORT";
    public static DBNAME = "DBNAME";
    public static DBUSER = "DBUSER";
    public static DBPWD = "DBPWD";
    public static AUTHDB = "AUTHDB";

    public static APIDBADDRESS = "APIDBADDRESS";
    public static APIDBPORT = "APIDBPORT";
    public static APIDBNAME = "APIDBNAME";
    public static APIDBUSER = "APIDBUSER";
    public static APIDBPWD = "APIDBPWD";
    public static APIAUTHDB = "APIAUTHDB";

    public static STRIPEAPIKEY = "STRIPEAPIKEY";
    public static STRIPEPUBLICKEY = "STRIPEPUBLICKEY";
    public static SINCHAPIKEY = "SINCHAPIKEY";
    public static EMAILACCESSKEY = "EMAILACCESSKEY";
    public static EMAILSECRETACCESSKEY = "EMAILSECRETACCESSKEY";
    public static EMAILSERVICEURL = "EMAILSERVICEURL";
    public static EMAILRATELIMIT = "EMAILRATELIMIT";
    public static APIURL = "APIURL";
    public static ENCKEY = "ENCKEY";

    public static EMAILHOST = "EMAILHOST";
    public static EMAILPORT = "EMAILPORT";
    public static EMAILUSERNAME = "EMAILUSERNAME";
    public static EMAILPASS = "EMAILPASS";

    public static PUBLICDIR = "PUBLICDIR";
    public static STATICDOMAIN = "STATICDOMAIN";
    public static SMSKEY = "SMSKEY";
    public static SMSSECRET = "SMSSECRET";
    public static PAYMENTPROVIDER = "PAYMENTPROVIDER";

    public nodeenv: string;
    public pagetrack: string;
    public port: number;
    public dbaddress: string;
    public dbport: number;
    public dbname: string;
    public dbuser: string;
    public dbpwd: string;
    public authdb: string;

    public apidbaddress: string;
    public apidbport: number;
    public apidbname: string;
    public apidbuser: string;
    public apidbpwd: string;
    public apiauthdb: string;

    public stripeApi: string;
    public stripePublic: string;
    public sinchApi: string;
    public emailAccessKey: string;
    public emailSecretAccessKey: string;
    public emailServiceUrl: string;
    public emailRateLimit: number;
    public apiUrl: string;
    public enckey: string;

    public emailHost: string
    public emailPort: number;
    public emailUsername: string;
    public emailPass: string;

    public publicDir: string;
    public staticDomain: string;
    public smsKey: string;
    public smsSecret: string;
    public paymentProvider: string;

    public projectDir: string;

    public versionKey: number;


    public get(key?: string, cb?: nconf.ICallbackFunction) {
        return nconf.get(key, cb);
    }

    constructor() {
        nconf.argv().env().file(path.join(__dirname, "../config.json"));
        this.nodeenv = this.get(Config.NODEENV) || 'development';
        this.pagetrack = this.get(Config.PAGETRACK) || 'true';
        this.port = this.get(Config.PORT) || process.env.PORT || 3000;
        this.dbaddress = this.get(Config.DBADDRESS) || '127.0.0.1';
        this.dbport = this.get(Config.DBPORT) || 3306;
        this.dbname = this.get(Config.DBNAME) || 'kasaptanal-local';
        this.dbuser = this.get(Config.DBUSER) || '';
        this.dbpwd = this.get(Config.DBPWD) || '';
        this.authdb = this.get(Config.AUTHDB) || 'admin';

        this.apidbaddress = this.get(Config.APIDBADDRESS) || '127.0.0.1';
        this.apidbport = this.get(Config.APIDBPORT) || 27017;
        this.apidbname = this.get(Config.APIDBNAME) || 'kasaptanal-api';
        this.apidbuser = this.get(Config.APIDBUSER) || '';
        this.apidbpwd = this.get(Config.APIDBPWD) || '';
        this.apiauthdb = this.get(Config.APIAUTHDB) || 'admin';

        this.stripeApi = this.get(Config.STRIPEAPIKEY) || '';
        this.stripePublic = this.get(Config.STRIPEPUBLICKEY) || '';
        this.sinchApi = this.get(Config.SINCHAPIKEY) || '';
        this.emailAccessKey = this.get(Config.EMAILACCESSKEY) || '';
        this.emailSecretAccessKey = this.get(Config.EMAILSECRETACCESSKEY) || '';
        this.emailServiceUrl = this.get(Config.EMAILSERVICEURL) || '';
        this.emailRateLimit = this.get(Config.EMAILRATELIMIT) || 15;
        this.apiUrl = this.get(Config.APIURL) || 'http://localhost:3001';
        this.enckey = this.get(Config.ENCKEY);

        this.emailHost = this.get(Config.EMAILHOST) || '';
        this.emailPort = this.get(Config.EMAILPORT) || 8889;
        this.emailUsername = this.get(Config.EMAILUSERNAME) || '';
        this.emailPass = this.get(Config.EMAILPASS) || '';

        this.publicDir = this.get(Config.PUBLICDIR) || '';
        this.staticDomain = this.get(Config.STATICDOMAIN) || '';

        this.smsKey = this.get(Config.SMSKEY) || '';
        this.smsSecret = this.get(Config.SMSSECRET) || '';
        this.paymentProvider = this.get(Config.PAYMENTPROVIDER) || 'iyzico';

        this.projectDir = path.join(__dirname, "../");

        this.versionKey = new Date(fs.statSync(path.join(this.projectDir, "package.json")).mtime).getTime();

    }
}

export default new Config();